import React, { useEffect, useState } from 'react'
import s from './ProfileInfo.module.css'
import { Field, Form } from 'react-final-form'
import { Input, Textarea } from '../../common/FormControl/FormControl'

const ProfileContactForm = ({ userProfile, toggleEditMode, saveProfileInfo, inputError }) => {

    let [errors, setError] = useState(inputError)

    useEffect(() => {
        setError(inputError)
        return () => errors.length = 0
    }, [inputError, errors])

    const onSubmit = (values) => {
        saveProfileInfo(values).then(
            () => {
                toggleEditMode(false)
            }
        )
    }

    return <Form
        onSubmit={onSubmit}
        render={({ handleSubmit, submitting, pristine, }) => (
            <form onSubmit={handleSubmit}>
                <h3>Main information:</h3>
                <div className={s.mainItem}>
                    <label>Enter your full name</label>
                    <Field name="fullName" validate={null}
                        render={Input} initialValue={userProfile.fullName} />
                </div>
                <div className={s.mainItem}>
                    <label>Write about yourself</label>
                    <Field name="aboutMe" validate={null}
                        render={Textarea} initialValue={userProfile.aboutMe} />
                </div>
                <div className={s.mainItem}>
                    <label>Do you looking for a job?</label>
                    <Field name="lookingForAJob" type="checkbox" validate={null}
                        render={Input} initialValue={userProfile.lookingForAJob} />
                </div>
                <div className={s.mainItem}>
                    <label>Job description</label>
                    <Field name="lookingForAJobDescription" validate={null}
                        render={Input} initialValue={userProfile.lookingForAJobDescription} />
                </div>

                <h3>Contacts:</h3>
                {errors.length > 0 ? <span>{errors}</span> : null}
                {
                    Object.keys(userProfile.contacts).map(link => <div className={s.contactItem} key={link}>
                        <label>{link}</label>
                        <Field name={'contacts.' + link} placeholder="url" initialValue={userProfile.contacts[link]} render={Input} />
                    </div>)
                }
                <button disabled={submitting || pristine}>Submit</button>
            </form >
        )}
    />
}

export default ProfileContactForm