import React from 'react';
import s from './Post.module.css'

const Post = (props) => {

    return (
        <div className={s.post}>
            <div className={s.headerPost}>
                <img className={s.avatar} src={props.avatarImg} alt="" />
                <span>{props.loginName}</span>
            </div>
            <p>{props.name}</p>
            {props.image ? <img className={s.postImage} src={props.image} alt="" /> : null}
            <span onClick={() => props.toggleLike(props.id)} className={!props.liked ? s.notLiked : s.liked}>&#9829; {props.like}</span>
        </div>
    )
}

export default Post;